<template>
	<div class="saleedi-login">
		<div class="login-welcome clearfix">
			<div class="welcome-text fl-l">您好，欢迎您注册并登录！</div>
			<img class="welcome-login fl-r" src="@/assets/images/img/logo-login.png" alt="">
		</div>
		<div class="login-form">
			<van-cell-group class="login-cell-group" :border="false">
				<van-field
					v-model="urserName"
					ref="urserName"
					class="login-cell"
					placeholder="请输入用户名"
					:error-message="errorMessage.urserName"
					@keyup.enter.native="onKeyupInput('urserName')">
					<img class="login-cell-icon" slot="left-icon" src="@/assets/images/icon/icon-ursename.png">
				</van-field>
				<van-field
					v-model="phoneNumber"
					ref="phoneNumber"
					class="login-cell"
					placeholder="请输入手机号，必填"
					:error-message="errorMessage.phoneNumber"
					type="number"
					pattern="\d*"
					@keyup.enter.native="onKeyupInput('phoneNumber')">
					<img class="login-cell-icon phoneNumber-icon" slot="left-icon" src="@/assets/images/icon/icon-phoneNumber.png">
					<van-button
						class="verification-btn"
						slot="button"
						size="small"
						type="primary"
						round
						:disabled="verificationTimeComputed"
						@click="getVerificationCode">
							验证码
							<span v-show="verificationTimeComputed"> ( {{verificationTime}} ) </span>
						</van-button>
				</van-field>
				<van-field
					v-model="verificationCode"
					ref="verificationCode"
					class="login-cell"
					placeholder="请输入验证码"
					type="number"
					:error-message="errorMessage.verificationCode"
					@keyup.enter.native="onKeyupInput('verificationCode')">
					<img class="login-cell-icon" slot="left-icon" src="@/assets/images/icon/icon-verification.png">
				</van-field>
			</van-cell-group>
			<van-button class="login-btn" type="primary" round @click="loginHanle">注册并登陆</van-button>
		</div>
	</div>
</template>

<script>
import { inputAutoFocusMoblie } from '@/js/lib/form'
export default {
	name: 'login',
	data () {
		return {
			formFieldRef: ['urserName', 'phoneNumber', 'verificationCode'],
			urserName: '',
			phoneNumber: '',
			verificationCode: '',
			errorMessage: {
				urserName: '',
				phoneNumber: '',
				verificationCode: ''
			},
			verificationTime: ''
		}
	},
	destroyed () {
		this.clearVerificationTime()
	},
	computed: {
		verificationTimeComputed () {
			if (this.verificationTime === '' || this.verificationTime < 0) {
				return false
			} else {
				return true
			}
		}
	},
	methods: {
		onKeyupInput (refName) {
			inputAutoFocusMoblie.apply(this, [refName, this.formFieldRef]).then(() => {
				this.loginHanle()
			})
		},
		loginHanle () {
			this.validate().then(validateFlag => {
				validateFlag && this.loginPose()
			})
		},
		loginPose () {
			const params = {
				userName: this.urserName,
				telephone: this.phoneNumber,
				authCode: this.verificationCode
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/register`,
				data: params
			}).then(res => {
				this.$router.push('/bindEnt')
			})
		},
		validate () {
			return new Promise(resolve => {
				this.clearValidate()
				let validateFlag = true
				if (this.urserName === '') {
					this.errorMessage.urserName = '用户名不能为空'
					validateFlag = false
				}
				if (this.phoneNumber === '') {
					this.errorMessage.phoneNumber = '手机号不能为空'
					validateFlag = false
				} else if (!/^[1][0-9]{10}$/.test(this.phoneNumber)) {
					this.errorMessage.phoneNumber = '手机号格式不正确'
					validateFlag = false
				}
				if (this.verificationCode === '') {
					this.errorMessage.verificationCode = '验证码不能为空'
					validateFlag = false
				}
				resolve(validateFlag)
			})
		},
		clearValidate () {
			for (let key in this.errorMessage) {
				this.errorMessage[key] = ''
			}
		},
		validatePhoneNumber () {
			return new Promise(resolve => {
				this.errorMessage.phoneNumber = ''
				let validateFlag = true
				if (this.phoneNumber === '') {
					this.errorMessage.phoneNumber = '手机号不能为空'
					validateFlag = false
				} else if (!/^[1][0-9]{10}$/.test(this.phoneNumber)) {
					this.errorMessage.phoneNumber = '手机号格式不正确'
					validateFlag = false
				}
				resolve(validateFlag)
			})
		},
		getVerificationCode () {
			this.validatePhoneNumber().then(validateFlag => {
				validateFlag && this.verificationCodeHandle()
			})
		},
		verificationCodeHandle () {
			const params = {
				telephone: this.phoneNumber
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/getVerificationCode`,
				data: params
			}).then(res => {
				this.verificationTimeHandle()
			})
		},
		verificationTimeHandle () {
			this.verificationTime = 10
			this.verificationTimeInterval = window.setInterval(() => {
				if (this.verificationTime < 0) {
					this.clearVerificationTime()
					this.verificationTime = ''
				} else {
					this.verificationTime--
				}
			}, 1000)
		},
		clearVerificationTime () {
			if (this.verificationTimeInterval) {
				window.clearInterval(this.verificationTimeInterval)
			}
		}
	}
}
</script>
